.intro{
    color: white;
}

.section-main
{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 7%;
    transform: scale(1);    
}

@media (max-width: 812px) {
  .section-main{
    align-items: center;
    flex-direction: column;
  }
}
@media (max-width: 812px) {
  .section1 { order: 2; }
  .section2 { order: 1; }
  
}
  
.icons
{
  display: flex;
  gap:5px;
}

@media (max-width: 812px) {
  .icons{
    display: none;
  }
  
}

.iconshid
{
  display: none;
}

@media (max-width: 812px) {
  .iconshid {
    display:contents;
    display: flex;
    gap: 10px;
    align-items: center;
    transform: scale(1.2);
  }
  
}

.sectioncontent
{
  font-family: 'Segoe   UI', Tahoma, Geneva, Verdana, sans-serif;
  height: 300px;
  width: 300px;
  transform: scale(1.1);
  
}

@media (max-width:812px) {
  .sectioncontent{
    margin-top: 50px;
    transform: scale(1);
  }
}
.font1
{
  font-size: 35px;
}

.sectioncontent p
{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .btn-resume
  {
    border: 3px solid;
    border-color:rgb(12, 141, 77);
    margin-top: 15px;
    font-weight:bolder;
    border-radius: 40px;
    padding: 10px 10px 10px 10px;
    text-decoration: none;
    color: rgb(255, 255, 255);
    background-color: black;
    
  }
  .btn-resume:hover
  {
  font-weight:bolder;
  color: #1d9465;
  background-color:black;
}


.section2{
  height: 300px;
   width: 325px;
}
@media (max-width: 812px) {
  .section2 {
    margin-top: 50px;
    width: 325px;             /* Smaller size for mobile */
    height: 195px;
    display: flex; 
    flex-direction: column;
    align-items: center;
    gap: 10px;
      
  }
}



.sectionimage
{
  height: 300px;
  width: 325px;
   border-radius: 47% 50% 57% 43% / 36% 30% 70% 64% ;
   background-image: url(/public/pic.png);
   background-position: center;
   background-repeat: no-repeat;
   background-color: #1d9465;
   border:2px #1d9465;
   
}
@media (max-width: 812px) {
  .sectionimage {
    width: 130px;             /* Smaller size for mobile */
    height: 130px;
    border-radius: 50%;   
    color: #d62a2a;    /* Makes it a perfect circle */
    background-color: #1d9465 ;
    border: 5px solid #ffffff;
    background-image: url(/public/picture.png);
    
    

    
  }
}

.sectionimage:hover{

  transform: scale(1.1);
  transition: 0.7s;
}

.icon
{
  text-decoration: none;
  color: #1d9465;
  
}